import { useEffect, useState } from 'react'
import { quotesDb } from '~/utils/quotesDb'
import { useLoaderData } from '@remix-run/react'

export const loader = async () => {
  return { quotesDb }
}

export default function QuoteCard() {
  const quotes = useLoaderData()
  const [index, setIndex] = useState()
  const [quote, setQuote] = useState('Your new quote will go here')
  const [author, setAuthor] = useState('The author of your new quote will go here')

  const randomQuote = (e) => {
    const random = Math.floor(Math.random() * quotes.length)
    setIndex(random)
    setQuote(quotes[index].quoteText)
    setAuthor(quotes[index].author)
  }

  return (
    <div id='quote-box' className='quote-box'>
      <h1>Here's your random quote</h1>
      <h2 id='quote'>{quote}</h2>
      <p id='author'>{author}</p>
      <button id='new-quote' className='new-quote' onClick={randomQuote}>New Quote</button>
      <a id='tweet-quote' href='twitter.com/intent/tweet' className='tweet-quote'>Tweet Quote</a>
    </div>
  )
}

